<template>
  <div>
    <div v-if="!cart.length" class="alert alert-secondary" role="alert">
      No Product In Cart!
    </div>
    <div
      v-if="orderPlaced"
      class="alert alert-success modal-header"
      role="alert"
    >
      <h5 class="modal-title">Order Successfully Placed!</h5>
      <button
        type="button"
        class="btn-close"
        data-bs-dismiss="modal"
        aria-label="Close"
        @click="() => (orderPlaced = false)"
      ></button>
    </div>

    <ul class="list-group">
      <li class="list-group-item" v-for="item in cart" :key="item.id">
        <div class="d-flex">
          <div class="flex-shrink-0">
            <img width="80px" :src="item.imgUrl" :alt="item.title" />
          </div>

          <div class="flex-grow-1 ms-3 media-body">
            <p class="mt-0">{{ item.title }}</p>
            <button
              @click="reduceQty(item.id)"
              class="btn-qty btn btn-sm btn-secondary"
            >
              -
            </button>
            x {{ item.qty }}
            <button
              @click="addQty(item.id)"
              class="btn-qty btn btn-sm btn-secondary"
            >
              +
            </button>
          </div>
          <button
            @click="removeItem(item.id)"
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
      </li>
    </ul>
    <div class="d-grid gap-2">
      <button
        :disabled="isProcessing"
        class="btn-checkout btn btn-success"
        v-if="cart.length"
        @click="placeOrder"
      >
        <span v-if="!isProcessing">Checkout (${{ totalPrice }})</span>
        <div v-else class="spinner-border" role="status">
          <span class="visually-hidden">Loading...</span>
        </div>
      </button>
    </div>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from "vuex";
  export default {
    name: "Cart",
    data() {
      return {
        isProcessing: false,
        orderPlaced: false,
      };
    },
    computed: {
      ...mapGetters(["cart"]),
      totalPrice() {
        return this.cart.reduce((a, b) => a + b.qty * b.price, 0);
      },
    },
    methods: {
      ...mapActions(["addQty", "reduceQty", "removeItem", "emptyCart"]),
      placeOrder() {
        this.isProcessing = true;
        setTimeout(() => {
          this.orderPlaced = true;
          this.isProcessing = false;
          this.emptyCart();
        }, 1000);
      },
    },
  };
</script>

<style scoped>
  .btn-qty {
    border-radius: 50%;
    width: 30px;
  }

  .media-body {
    text-align: left;
  }

  .btn-checkout {
    margin-top: 20px;
  }
</style>
